
html, body {
  height: 100%;
}
body {
  overflow-y: hidden;
}

div {
  page-break-inside: avoid;
}
li {
	margin-left: -1em;
}

#root {
  height: 100%;
}

.app {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.app-header {
  background: black;
  color: white;
  text-align: center;
  height: 42px;
  position: fixed;
  top: 0;
  /* flex-basis: 42px; */
  /* flex-shrink: 0; */
  /* flex-grow: 0; */
  width: 100%;
}
.app-header h1 {
  margin: 0;
  font-weight: 300;
}
.app-header img {
  height: 42px;
  width: 42px;
}

.app-contents {
  margin-top: 42px;
  display: flex;
  flex-direction: row;
  /* position: fixed; */
  /* top: 42px; */
  height: calc(100% - 42px);
}
.app-page {
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.app-footer {
  padding: 1em;
  /* float: right; */
  text-align: right;
}

.toc {
  display: none;
  overflow-y: auto;
  /* height: 100%; */
  flex-basis: 14em;
  flex-shrink: 0;
  color: rgb(26, 188, 156);
  font-size: 16px;
  line-height: 1.2em;
  list-style-image: none;
  list-style-position: outside;
  list-style-type: none;
  padding-top: 1em;
  /* padding-bottom: 2em; */
}
@media screen and (min-width: 500px) {
  .toc {
    display: block;
  }
}
.toc-title {
  color: black;
  padding: 4px;
  font-weight: bold;
  margin-bottom: 1em;
}
.toc a {
  display: block;
  padding: 1px 5px;
  color: rgb(26, 188, 156);
  cursor: pointer;
  text-decoration: none;
}
.toc a:visited {
  color: rgb(26, 188, 156);
}
.toc a:hover {
  background-color: #dbedff;
}
.toc-level {
  margin-bottom: 1em;
}
.toc-level-title {
  font-weight: bold;
}
.toc-rule {
  margin-left: 1em;
}


.app-rules {
  margin-left: 1em;
  /* height: 100%; */
  /* overflow-y: scroll; */
}

